export default {
	data() {
		return {
			index: 0,
			start_time: 0,
			end_time: 0
		};
	},
	methods: {
		fndbClick() {
			this.index = this.index == 0 ? 1 : this.index + 1;
			if (this.index == 1) {
				this.start_time = new Date().getTime();
				setTimeout(()=>{
					this.index = this.start_time = this.end_time = 0;
				},200)
			} else if (this.index == 2) {
				this.end_time = new Date().getTime();
				if (this.end_time - this.start_time < 300) {
					uni.showToast({
						title: '双击了屏幕',
						icon: 'none'
					})
				}
				this.index = this.start_time = this.end_time = 0;
			}
		}
	}
};
